@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

*{
  --color-one :#4f338f;
  --color-two :#aa7dff;
  --color-three :#e5d8ff;
  --color-four :#efebff;
  --font-size-one: 1.5rem;
  --font-size-two: 2rem;
  --font-size-three: 3rem;
  --font-size-four: 4rem;
  /* color: var(--color-four); */
  margin: 0px;

  font-family:'Dosis', sans-serif;
}
.aboutMe {
    padding: 8em 7em;
    background-color: var(--color-one);
    text-align: center;
    font-size: var(--font-size-one);
}

.aboutMe-title {
    margin-bottom: 1em;
    font-size: var(--font-size-three);
}

@media(max-width: 650px) {
    .aboutMe {
        padding: 2em 1em 3em;
        text-align: start;
        font-size: 1.6rem;
    }
    
    .aboutMe-title {
        text-align: center;
        font-size: 2.2rem;
    }
}
.presentation {
    background-image: url(/portfolio/static/media/freelancer.1dc1e34f.svg);
    height: 35em;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 30em;
    position: relative;
    display: grid;
    grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
    grid-template-columns: 10% 20% 20% 20% 20% 10%;
}

.presentation-helloWorld {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    grid-row: 2/4;
    grid-column: 3/5;
    color: var(--color-one);
}

.presentation-about {
    font-size: 1.5rem;
    color: var(--color-one);
    grid-row: 4/5;
    grid-column: 2/6;
    display: flex;
    /* border: solid 1px red; */
    justify-content: center;
    align-items: center;
}

.presentation-contactMe {
    grid-row: 9/11;
    grid-column: 4/5;
    background-color: var(--color-three);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .5s;
}

.presentation-contactMe:hover {
    border: 4px solid var(--color-two);
}

.presentation-contactMe p {
    color: var(--color-one);
    font-size: 1.5em;
}

.presentation-contactMe_gitTwitLinkendin {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.presentation-contactMe a img {
    width: 5em;
    display: flex;
    justify-content: center;
    align-items: column;
}

.presentation-contactMe_github {
    width: 3em !important;
}




@media(max-width: 650px) {
    .presentation-helloWorld {
        grid-row: 1/4;
        grid-column: 1/7;
    }

    .presentation-about {
        grid-row: 3/7;
        grid-column: 1/7;
        text-align: center;
    }

    .presentation-contactMe {
        grid-column: 4/6
    }

    .presentation-contactMe a img {
        width: 4em;
    }

    .presentation-contactMe_github {
        width: 2.5em !important;
    }
}
.skills{
    background-color: var(--color-three);
    color: var(--color-one);
    margin: -6em 15em 2em ;
    padding: 2em 3em;
    border-radius: 25px;
    display: grid;
    grid-template-columns: repeat(3,1fr) ;    
    
}
.skills div{
    color: var(--color-one);        
    font-size: var(--font-size-two) ;
}

.listSkills{
    margin-top: 1em;
}

.listSkills li{
    color: var(--color-one);        
    font-size: var(--font-size-one) ;
    list-style:none;
}

.skills-frontend{
    font-size: 3em;
    border-radius: 25px 0px 0px 25px;
}

.skills-backend{
    grid-column: 2/4;
    border-radius: 0px 25px 25px 0px;
}

/* Images (size) */
.image-logo{
   width: 100%;
}
@media(max-width: 650px){
    .skills{
        margin: -2em 1em 1em ;
    }
    
    .listSkills{
        padding: 0;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }
    .listSkills li{
        min-width: 50%;
    }
    
    .skills-frontend{
        font-size: 1.5em !important;
        grid-column: 1/5;
        border-radius: 25px 0px 0px 25px;
    }
    
    .skills-backend{
        display: none;
    }
}
.card{
    background-color: var(--color-one);
    border-radius: 5px;
    grid-column: 1/6;
    height:17em ;
    padding: .5em;
    display: grid;
    grid-template-columns: 39% 1%  20% 20% 20%  ;
    margin-bottom: 2em;
}
.card-left{
    
    grid-column:1/2;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.card-left_img{
    width: 80%;

}

.card-right{
    padding: .5em 0em;
    grid-column: 3/6 ;    
    display: grid;
    grid-template-rows: 5% 15% 60% 15% 5%;
    grid-template-columns: repeat(10, 10%) ;
}

.card-right > h2 {
    grid-row: 2/3;
    grid-column: 1/7;
}
.card-right > p {
    grid-row: 3/4;
    grid-column: 1/11;
    padding: 2em 5em 1em 1em;
    text-align:justify ;   
}

.card-right_appLink{
    margin: .25em;
    background-color: var(--color-four);
    border-radius: 10px;
    box-shadow: 0px 0px 15px var(--color-two);

    color: var(--color-one);
    font-size: 1.5rem;
    
    grid-row: 2/3;
    grid-column: 7/10;
    
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.card-right_tecnologies{
    grid-row: 4/5;
    grid-column: 1/11;
    display: flex;
    align-items: center;

}

.card-right_tecnologies > p{
    margin-right: .5em;        
}
.card-right_tecnologies > img{
    width: 2em;
    height: 2em;       
}

.proyects{
    background-color: var(--color-four);
    padding: 3em 5em;
    display: grid;
    grid-template-columns: repeat(5, 20%);
}
.curriculum {
    width: 100%;
    height: 30em;
}

.curriculum-item {
    width: 100%;
    height: 100%;
}
nav {
    width: 100%;
    height: 7rem;
    background-color: var(--color-three);
    display: grid;
    grid-template-rows: 30% 40% 30%;
    grid-template-columns: 5% 10% 5% 20% 10% 10% 5% 15% 15% 5%;
}

.nav-logo {
    grid-column: 2/3;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    width: 1.5em;
    height: 1.5em;
    background-color: var(--color-one);
    border-radius: 30px;
    color: white !important;
    text-decoration: none;
}

.nav-button {
    height: 100%;
    grid-column: 9/10;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-three);
    font-weight: 700;
    background-color: var(--color-one);
    border-radius: 5px;
    font-size: 2rem;
    text-decoration: none;
}

.nav-button_proyects {
    margin-right: 1em;
    grid-column: 8/9;
}

@media(max-width: 650px) {
    nav {
        width: 100%;
    }

    .nav-button {
        display: none;
    }

    .nav-button_proyects {
        display: none;

    }
}
.socialMedia{
    display: flex;
    justify-content: center;
    align-items: center;
}

.socialMedia-logos{
    width: 6em;
}
.socialMedia-logos_github{
    width: 4em;
}
.footer{
    display: flex;
    padding: 3em 0em 0em;
    background-color: var(--color-three );
    text-align: center;
    justify-content: center;
    align-items: center;
}
.footer-socialMedia p{
    color:var(--color-one);
    font-size: var(--font-size-two);
}
